import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import "../model/PhoneInterface";
import {Phone} from '../model/PhoneInterface';

@Injectable({
  providedIn: 'root'
})
export class QRcodeServiceService {

  constructor(private http: HttpClient) { }

  qrCodeURI = "/qr-generator/qr-code";
  pdf417URI = "/qr-generator/pdf-417";
  dataMatrixURI = "/qr-generator/data-matrix";

getQrCode() {
  return this.http.get(this.qrCodeURI);
}

getPDF417() {
  return this.http.get(this.pdf417URI);
}

getDataMatrixCode() {
  return this.http.get(this.dataMatrixURI);
}


}
