import { Component, OnInit } from '@angular/core';
import {Phone} from '../model/PhoneInterface';
import {QRcodeServiceService} from '../services/qrcode-service.service';

@Component({
  selector: 'qr-generator',
  templateUrl: './qr-generator.component.html',
  styleUrls: ['./qr-generator.component.scss']
})
export class QrGeneratorComponent implements OnInit {

  ngOnInit() {
  }

  constructor(private qrService: QRcodeServiceService) {}
  base64String = "";
  phoneObj: any = new class implements Phone {
    battery: string;
    brand: string;
    launchDate: string;
    mainCamera: string;
    memory: string;
    model: string;
    network: string;
    os: string;
    selfieCamera: string;
    sensors: string;
    sound: string;
    base64Image: string;
  };
getQRCode() {
  this.qrService.getQrCode().subscribe(
    data => {
      this.phoneObj = data;
      console.log(data);
      console.log(data['base64Image']);
      this.base64String = data['base64Image'];
    }, error => console.log(error));
}

getPDF417() {
  this.qrService.getPDF417().subscribe(data => {
    this.phoneObj = data;
    console.log(data);
    console.log(data['base64Image']);
    this.base64String = data['base64Image'];
  }, error => console.log(error));
}

  getDataMatrixCode() {
    this.qrService.getDataMatrixCode().subscribe(data => {
      this.phoneObj = data;
      console.log(data);
      console.log(data['base64Image']);
      this.base64String = data['base64Image'];
    }, error => console.log(error));
  }

onChange(selectedValue) {
  if(selectedValue === "qr_code") {
    console.log(selectedValue);
    this.getQRCode();
  } else if(selectedValue === "pdf_417") {
    console.log(selectedValue);
    this.getPDF417();
  } else if(selectedValue === "data_matrix") {
    console.log(selectedValue);
    this.getDataMatrixCode();
  }

}




}
